export default{
  data() {
    return {
      receive: {}
    }
  },
  onLoad(option) {
    if(option.item) {
      this.receive = JSON.parse(option.item)
    }
    console.log('分享文章详情页接受到的参数',JSON.parse(option.item))
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    copyAction(num) {
      uni.setClipboardData({
        data: num,
        success: function () {
          uni.showToast({
            title: '复制成功'
          })
        }
      })
    },
    goGrade() {
      uni.navigateTo({
        url: 'grade'
      })
    }
  }
}
